html {
    font-family: Lato, Roboto, Arial, Helvetica, sans-serif;
    box-sizing: content-box;
}

html * {
    box-sizing: inherit;
}

header {
    display: flex;
    justify-content: center;
    background-color: #fff;
    padding: 22px;
    box-shadow: 1px 2px 5px rgb(0, 0, 0, 0.3);
    z-index: 2;
    position: relative;
}

header input{
    outline: none;
    border: solid 1px rgb(219, 219, 219);
    padding: 10px 5px;
    border-radius: 4px;
}